import React from "react";
import { Container } from "react-bootstrap";
import { getPreviewLink } from "./storage";
import { getCollection } from "./api";

function CollectionCard(props) {
  const domRef = React.useRef();
  const [isVisible, setVisible] = React.useState(false);
  const [previewLink, setPreviewLink] = React.useState(null);
  const [collection, setCollection] = React.useState(null);

  React.useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setVisible(true);
        }
      });
    });
    observer.observe(domRef.current);
    return () => {
      try {
        observer.unobserve(domRef.current);
      } catch {}
    };
  }, []);

  React.useEffect(() => {
    (async () => {
      if (isVisible) {
        const collection = await getCollection({ handle: props.handle });
        setCollection(collection);

        if (collection !== null) {
          setPreviewLink(await getPreviewLink(collection.thumbnail));
        }
      }
    })();
  }, [isVisible, props.handle]);

  return (
    <Container className="collection-card" ref={domRef}>
      <div className="d-flex flex-column">
        <img className="mx-auto mt-4 mb-3" src={previewLink} />
        <span className="mx-auto mb-2 title">{collection !== null && collection.name}</span>
        <span className="truncate text-muted description">
          {collection !== null && collection.description}</span>
      </div>
    </Container>
  );
}

export default CollectionCard;
